import React from 'react';
import Card from './Card';
import Header from './Header';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import { NavLink } from 'react-router-dom';

function Main(props) {

    const userData = React.useContext(CurrentUserContext);
    return (
        <>
        <Header>
            <NavLink exact to="/" activeClassName="menu__link_active" className="menu__link">{props.userMail.email}</NavLink>
            <NavLink onClick={props.singOut} to="/sign-in" className="menu__link">–í—ã–π—Ç–∏</NavLink>
        </Header>
        <main className="content">
            <section className="profile">
            <div className="profile__container">
                <div className="profile__image-container">
                    <button aria-label="–ò–∑–º–µ–Ω–µ–Ω–∏–µ –∏–º–µ–Ω–∏ –∏ –æ–ø–∏—Å–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è" type="button" className="profile__image-redact"></button>
                    <img src={userData.avatar} alt="–§–æ—Ç–æ–≥—Ä–∞—Ñ–∏—è –ø—Ä–æ—Ñ–∏–ª—è" className="profile__image" onClick={props.onEditAvatar}/>
                </div>
                <div className="profile__container-info">
                <div className="profile__container-name">
                    <h1 className="profile__name">{userData.name}</h1>
                    <button aria-label="–ò–∑–º–µ–Ω–µ–Ω–∏–µ –∏–º–µ–Ω–∏ –∏ –æ–ø–∏—Å–∞–Ω–∏—è –ø—Ä–æ—Ñ–∏–ª—è" type="button" className="profile__redact" onClick={props.onEditProfile}></button>
                </div>
                <p className="profile__description">{userData.about}</p>
                </div>
            </div>
            <button aria-label="–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–µ–∫ —Å –º–µ—Å—Ç–∞–º–∏" type="button" className="profile__add-profile" onClick={props.onAddPlace}></button>
            </section>
    
            <section className="places">
            <ul className="places__cards">
                {props.initialCards.map((card) => (
                    <Card
                        openedCard={card}
                        onCardClick={props.—ÅardClick}
                        cardName={card.name}
                        cardImage={card.link}
                        key={card._id}
                        cardLikes={card.likes}
                        onCardLike={props.onCardLike}
                        onCardDelete={props.onCardDelete}
                    />
                    )
                )}
            </ul>
            </section>
        </main>
        </>
    );
}
export default Main;